import back from "../../components/back.vue";
import keyboard from "../../components/keyboard.vue";
export default {
	data() {
		return {
			phoneNum:'',
			errorMsg:'',
			showFlag:0,
			valueList:[],
			vercode:'重新获取',
			second:60
		}
	},
	methods: {
		bindKeyEvent(val){
			var that = this;
			that.valueList = val
			if(that.valueList.length==6){
				uni.request({
				    url: that.serverUrl+'/user/login', 
					method:'POST',
				    data: {
				       phone:that.phoneNum,
					   devCode:that.cid(),
					   key:that.valueList.join(''),
					   type:0
				    },
				    header: {
				        'content-type': 'application/x-www-form-urlencoded'
				    },
					sslVerify:false,
				    success: (res) => {
						if(res.data.resultCode=='10000'){
							var userInfo = eval(res.data.data);
							userInfo.phoneNum=that.phoneNum;
							uni.setStorageSync('userInfo',userInfo);
							uni.switchTab({
								url:'../information/information'
							})
						}else{
							that.errorMsg=that.showError(res.data.msgCode,res.data.msg);
							that.showFlag=1
							setTimeout(function() {
								that.errorMsg='';
								that.showFlag=0;
							}, 5000);
						}
				    },
					fail:(re)=>{
						uni.showModal({
						    title: '提示',
						    content: '网络异常，请检查网络后重试',
							showCancel:false,
						    success: function (res) {
						        if (res.confirm) {
						            console.log('用户点击确定');
						        } else if (res.cancel) {
						            console.log('用户点击取消');
						        }
						    }
						});
					}
				});
			}
		},
		getVerCode(e){
							var that = this;
							uni.request({
								url: that.serverUrl+'/user/authCode', 
								method:'POST',
								data: {
								   phone:that.phoneNum,
								   devCode:that.cid(),
								   type:0
								},
								header: {
									'content-type': 'application/x-www-form-urlencoded'
								},
								sslVerify:false,
								success: (res) => {
									that.loadingFlag=true;
									if(res.data.resultCode=='10000'){
										
									}else{
										that.errorMsg=that.showError(res.data.msgCode,res.data.msg);
										that.showFlag=1
										setTimeout(function() {
											that.errorMsg='';
											that.showFlag=0;
										}, 5000);
									}
								},
								fail:(re)=>{
									uni.showModal({
										title: '提示',
										content: '网络异常，请检查网络后重试',
										showCancel:false,
										success: function (res) {
											if (res.confirm) {
												console.log('用户点击确定');
											} else if (res.cancel) {
												console.log('用户点击取消');
											}
										}
									});
								}
							});
							this.readSecond();
					},
					readSecond(){
						var that=this;
						if(that.second!=60){
							return;
						}
						that.second--;
						var intervalID =setInterval(function(){
							that.vercode ="重新获取("+that.second--+")";
						},1000);
						setTimeout(function(){
							clearInterval(intervalID);
							that.vercode ="重新获取";
							that.second=60;
						},61000);
					},
	},
	
	onLoad(data) {
		this.phoneNum=data.phoneNum;
		this.readSecond();
	},
	components: {
		back,
		keyboard
	}
}
